.inteligo {
  body.auth-login .login-sidebar {
    width: 100%;
  }

  body.auth-login .layout-content {
    margin-top: 87px;
  }

  .form-box {
    form {
      width: auto;
      background: none;
      padding-bottom: 20px;
    }
  }

  div.form-substitute, .auth-login.form {
    width: 984px;
    margin: 0 auto;
    padding: 1px 0 147px;
    background: url(https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/skins/inteligo/img/page.png) no-repeat -1042px 100%;
    position: relative;
    min-height: 128px;
  }

  div.required-note {
    .form-required {
      display: inline !important;
    }

    display: block !important;
    color: #ff0000;
  }

  &-loader {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    min-height: 150px;
    background: rgba(255, 255, 255, .5);

    .loader_box {
      width: 40px;
      height: 40px;

      .loader {
        width: 100%;
        height: 100%;
        border: .2em solid #5195c4;
        border-bottom-color: transparent;
        border-radius: 50%;
        animation: .6s loader-03 linear infinite;
      }
    }
  }

  .broke {
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
  }

  .box-login {
    width: 350px;
    border: 4px solid #cddbe6;
    padding: 42px 16px 12px 12px;
    background: #fff;
    height: 160px;
  }

  .box-login-long h1, .box-login h1 {
    width: 350px;
    padding: 9px 20px 24px;
  }

  div.form-substitute, .auth-login.form {
    background-position: -1222px 100%;
  }
}
  