$blue: #003574;
@import "../mixins";
.ipko {
  body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &_header {
    &_content {
      height: 96px;
      display: flex;
      align-items: center;

      img {
        width: 75px;
      }
    }
  }

  &_main {
    padding: 40px 0;
  }

  &_form {
    h2 {
      line-height: 40px;
      padding: 2px 0 6px;
      font-size: 33px;
      font-weight: 400;
    }

    &_header {
      padding-bottom: 16px;
    }

    .error {
      input {
        border: 1px solid red;

        &:focus {
          border: 1px solid red;
        }
      }

      &_message {
        color: red;
        padding-top: 10px;
        font-size: 12px;
      }
    }

    .edit {
      .error_message {
        display: none;
      }
    }

    input {
      padding: 12px 0 10px 20px;
      min-height: 48px;
      font-size: 16px;
      line-height: 24px;
      border-radius: 4px;

      &:focus {
        box-shadow: none;
        outline: none;
        border-color: #0a0a0a;
      }
    }

    .btn_container {
      padding: 15px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      a {
        color: $blue;
        text-decoration: none;
        font-size: 13px;
        line-height: 16px;
      }

      .btn {
        min-width: 135px;
        background: $blue;
        color: #fff;
        font-size: 15px;
        line-height: 24px;
        border-radius: 4px;
        border: 1px solid $blue;
        padding: 8px 30px 6px 31px;
      }
    }
  }

  &_banner {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: -7%;
      width: 1px;
      height: 100%;
      background: #e5e5e5;

    }

    &_content {
      display: flex;

      img {
        width: 208px;
        height: 208px;
      }

      .special {
        margin-left: 32px;

        h3 {
          font-size: 23px;
          line-height: 32px;
          margin-bottom: 0;
        }

        p {
          font-size: 15px;
          line-height: 24px;
          padding-top: 7px;
          margin-bottom: 24px;
        }

        .btn {
          border-color: transparent;
          padding: 5px;
          border-radius: 4px;
          min-width: 135px;
          //min-width: 135px;
          background-color: #db912c;
          color: #fff;
          font-size: 15px;
        }
      }
    }
  }

  &_news {
    border-top: 1px solid #e5e5e5;
    margin-top: 40px;
    padding: 38px 0 10px;

    &_content {
      display: flex;

      .new {
        padding: 0 15px;
        flex-grow: 1;
        flex-basis: 0;

        p {
          font-size: 13px;
          line-height: 16px;
          margin: 0;
          color: #000;
          padding-top: 4px;
          padding-bottom: 4px;
          font-weight: lighter;

          em {
            font-size: 11px;
            line-height: 16px;
            margin: 0;
            color: #000;
            font-weight: 400;
            padding: 5px 0 3px;
            color: #636363;
            display: inline-block;
            font-style: inherit;
          }
        }

        h4 {
          font-size: 17px;
          line-height: 24px;
          margin: 0;
          color: #000;
          font-weight: 400;
          padding: 0;
        }
      }
    }
  }

  &_footer {
    margin-top: auto;
    border-top: 1px solid #e5e5e5;
    max-width: 100%;
    -webkit-box-shadow: 0 -8px 0 #f2f2f2;
    box-shadow: 0 -8px 0 #f2f2f2;

    &_content {
      display: flex;
      align-items: center;
      height: 64px;
    }

    &_links {
      display: inline-block;
      margin: 0 auto 0 16px;

      a {
        font-size: 13px;
        line-height: 16px;
        font-weight: 400;
        padding-top: 4px;
        padding-bottom: 4px;
        margin: 0 40px 0 0;
        color: #636363;
        text-decoration: none;
      }
    }

    &_icons {
      display: inline-block;
      margin: 0 16px 0 auto;
      float: right;

      a {
        font-size: 15px;
        line-height: 24px;
        font-weight: 400;
        padding-top: 7px;
        padding-bottom: 1px;
        margin: 0 0 0 40px;
        color: #000;
        text-decoration: none;
      }

      .security:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        padding-left: 8px;
        position: relative;
        bottom: 1px;
        background: url(data:image/svg+xml;base64,) no-repeat;
        width: 24px;
        height: 24px;
      }

      .pormoc:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        padding-left: 8px;
        position: relative;
        bottom: 1px;
        background: url(data:image/svg+xml;base64,) no-repeat;
        width: 24px;
        height: 24px;
      }

      .message:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        padding-left: 8px;
        position: relative;
        bottom: 1px;
        background: url(data:image/svg+xml;base64,) no-repeat;
        width: 24px;
        height: 24px;
      }
    }

  }

  .loader_wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, .5);

    .loader_box {
      width: 40px;
      height: 40px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .loader {
        width: 100%;
        height: 100%;
        border: .2em solid $blue;
        border-bottom-color: transparent;
        border-radius: 50%;
        animation: .6s loader-03 linear infinite;
      }
    }
  }

  &_fail {
    display: flex;
    align-items: center;
    padding-top: 20px;

    &_content {
      margin-left: 25px;

      h3 {
        font-size: 23px;
        line-height: 32px;
        color: #db912c;
      }

      p {
        font-size: 14px;
      }
    }
  }


  @include mediaDown(768px) {
    &_header {
      border-bottom: 1px solid #d5d5d5;

      &_content {
        height: 63px;
      }
    }
    &_main {
      padding-top: 0;
    }
    &_form {
      margin-top: 16px;

      &_header {
        font-size: 23px;
        line-height: 32px;
      }

      .btn_container {
        flex-direction: column-reverse;
        align-items: flex-start;

        a {
          font-size: 15px;
          padding-top: 7px;
        }

        .btn {
          width: 100%;
          margin: 8px 0;
        }

      }
    }
    &_banner {
      margin-top: 20px;
      padding-top: 18px;
      border-top: 1px solid #e5e5e5;

      &:before {
        content: none;
      }

      &_content {
        width: 100%;

        img {
          display: none;
        }

        .special {
          margin-left: 0;
          width: 100%;

          h3 {
            font-size: 19px;
          }

          .btn {
            width: 100%;
          }
        }
      }
    }
    &_news {
      margin-top: 20px;
      padding-top: 18px;

      &_content {
        margin: -8px 0;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        flex-wrap: wrap;
      }

      .new {
        padding: 0 0 10px;
      }
    }
    &_footer {
      box-shadow: 0 -16px 0 #f2f2f2;

      &_content {
        height: 256px;
        flex-direction: column-reverse;
        padding-top: 24px;
      }

      &_links, &_icons {
        display: flex;
        flex-direction: column;
        margin: 0 auto 0 16px;
        height: 50%;
      }

      &_icons {
        float: none;

        a {
          margin-left: 0;
        }
      }
    }
  }
}