$orange: #ff6200;
@import "../mixins";
.ingbank {
  body {
    background-image: url('../../img/ingbank/bg.jpg');
    width: 100%;
    min-height: 100vh;
    overflow: hidden auto;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    text-align: center;
  }

  color: #333;

  &_form_container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: calc(100vh - 125px);
  }

  &_form_error {
    flex-direction: column;
    margin-top: 30px;

    svg {
      stroke: $orange;
      margin-bottom: 15px;
    }
  }

  &_form {
    margin-top: 20px;
    width: 520px;
    background-color: #fff;
    border-radius: 4px;
    padding: 13px 20px;

    &_header {
      text-align: center;
      //height: 128px;
      padding: 20px 0 10px;

      img {
        padding: 0 0 10px;
        width: 128px;
        display: block;
        margin: 0 auto;
      }
    }

    &_body {
      min-height: 300px;
      position: relative;

      &.error {
        min-height: 400px;
      }
    }

    .error_message {
      margin: 0 -20px;
      background: rgba(255, 244, 229, .95);

      .error_content {
        width: 280px;
        margin: 0 auto;
        text-align: left;
        padding: 15px 0;
        position: relative;

        .icon {
          position: absolute;
          top: 12px;
          left: -30px;
        }

        h3 {
          color: red;
          font-size: 16px;
          line-height: 24px;
          font-weight: 600;
        }

        p {
          font-size: 14px;
          margin-bottom: 0;
        }
      }
    }

    &_login {
      padding: 20px 0;
      max-width: 280px;
      margin: 0 auto;

      label {
        text-align: left;
        font-size: 16px;
        line-height: 24px;
        display: block;
        margin: 8px 0;
      }

      input {
        -webkit-appearance: none;
        -webkit-box-shadow: none;
        -o-box-shadow: none;
        box-shadow: none;
        border-color: #a8a8a8;
        background-color: #fff;
        color: #333;
        margin: 0 0 6px;
        height: 48px;
        padding: 12px 16px;
        font-size: 16px;
        line-height: 24px;
        border-radius: 4px;
        transition: all .2s ease;

        &:hover, &.error {
          border-color: $orange;
        }

        &:focus {
          border-color: $orange;
          box-shadow: none;
        }
      }

      .error-message {
        color: $orange;
        display: block;
        font-weight: bold;
        text-align: left;
        margin-bottom: 20px;
      }

      .btn_container {
        text-align: right;
        padding-top: 10px;

        a {
          color: $orange;
          font-size: 16px;
          line-height: 24px;
        }
      }
    }

    h2 {
      font-size: 20px;
      padding: 10px 0 0;
    }

    .btn {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      min-height: 16px;
      outline: 0px;
      font-family: INGMe, arial, helvetica, sans-serif;
      -webkit-font-smoothing: antialiased;
      font-size: 19px;
      line-height: 28px;
      font-weight: bold;
      border: 1px solid rgb(255, 98, 0);
      border-radius: 8px;
      padding: 6px 15px 8px;
      background-color: rgb(255, 98, 0);
      color: rgb(255, 255, 255);
      position: relative;

      &:focus {
        box-shadow: none;
      }

      &.next {
        width: 100%;
      }
    }

  }

  &_news {
    padding: 24px 45px 20px;
    line-height: 20px;
    border-top: 2px solid rgba(0, 0, 0, .2);

    .new {
      display: flex;
      margin: 4px;

      span {
        width: 100px;
        padding-right: 15px;
        padding-top: 5px;
        font-size: 16px;
      }

      a {
        color: $orange;
        text-align: left;
        font-size: 16px;
        line-height: 24px;
      }
    }
  }

  &_footer {
    height: 125px;

    &_row {
      margin-top: 16px;

      ul.ing-list-hybrid {
        width: 100%;
        background-color: rgb(240, 240, 240);
        border-radius: 4px;
        display: flex;
        justify-content: space-between;
        margin: 0;
        padding: 0;
        list-style: none;

        li {
          position: relative;
          padding-left: 16px;
          padding-right: 17px;
          overflow: hidden;
          display: inline-block;
          vertical-align: middle;

          a {
            float: left;
            overflow: hidden;
            width: 100%;
            color: rgb(51, 51, 51);
            line-height: 48px;
            white-space: nowrap;

            svg {
              stroke: $orange;
              margin: 0px 16px 0px 0px;
              position: relative;
            }
          }
        }

        .pull-right-md {
          ul li:not(:first-child):before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0px;
            transform: translateY(-50%);
            height: 1.6rem;
            width: 1px;
            background-color: rgb(217, 217, 217);
          }
        }
      }
    }

    &_copyright {
      display: flex;
      color: #fff;
      align-items: center;
      justify-content: space-between;
      margin-top: 15px;

      ul.ing-list-inline {
        display: inline-block;
        margin-bottom: 0;

        li {
          display: inline-block;
          vertical-align: middle;
          line-height: 48px;
          position: relative;
          color: rgb(168, 168, 168);
          padding: 0 16px;

          &:not(:last-child)::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 0px;
            transform: translateY(-50%);
            height: 1.6rem;
            width: 1px;
            background-color: rgb(217, 217, 217);
          }
        }
      }
    }
  }

  a {
    color: #fff;
    text-decoration: none;
  }

  .loader_box {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .loader {
    width: 100%;
    height: 100%;
    border: .2em solid $orange;
    border-bottom-color: transparent;
    border-radius: 50%;
    animation: .6s loader-03 linear infinite;
  }

  @include mediaDown(768px) {
    background: #fff;
    &_form {
      margin-top: 0;
      width: 100%;
      padding: 0;

      &_header {
        text-align: left;
        padding: 8px 16px;
        box-shadow: 0 1px 8px 0 rgb(0 0 0 / 20%), 0 3px 3px -2px rgb(0 0 0 / 12%), 0 3px 4px 0 rgb(0 0 0 / 14%);

        img {
          width: 96px;
          //padding-bottom: 0;
          padding: 8px 0;
          margin: 0;
        }
      }

      &_body {
        padding: 20px 15px 0;
        //min-height: auto;
        h2 {
          text-align: left;
        }
      }

      &_login {
        max-width: 100%;
        padding-top: 15px;
        width: 100%;
      }
    }
    &_news {
      padding: 24px 15px 20px;

      .new {
        flex-direction: column;
        margin: 0;

        a {
          margin-left: 20px;
        }
      }
    }
    &_footer {
      margin-top: 0;

      &_row {
        ul.ing-list-hybrid {
          flex-direction: column;
          padding: 0 20px;

          li {
            padding: 2px 0;
            text-align: left;

            a {
              float: none;
              text-align: left;
            }
          }

          li.pull-right-md {
            ul {
              padding: 0;

              li {
                width: 100%;
                border-top: 1px solid #d9d9d9;

                &:not(:first-child):before {
                  content: none;
                }
              }
            }
          }
        }
      }

      &_copyright {
        margin-top: 15px;
        padding-bottom: 15px;

        ul.ing-list-inline li {
          color: #333;
          padding: 0;
          display: block;
          text-align: left;
          line-height: 24px;

          &:not(:last-child)::after {
            content: none;
          }
        }

        span.name {
          display: none;
        }

        ul {
          //padding-left: 0;
          padding: 0 15px;

          a {
            color: #333;
          }
        }
      }
    }

  }
}

@keyframes loader-03 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}