  .btn {
    display: inline-block;
    outline: medium none;
    margin: 0 0 10px;
    text-decoration: none;
    vertical-align: baseline;
    color: #fff;
    background-color: #ec0000;
    width: 100%;
    cursor: pointer;
    font-size: 15px;
    line-height: 20px;
    border-radius: 4px;
    box-shadow: 0 1px 4px 0 rgb(0 0 0 / 41%);
    border: none;
    height: 35px;
    font-weight: bold;
  }

