  .centrum_push_info {
    text-align: center;

    h1 {
      font-size: 18px;
      text-align: center;
      display: block;
      padding-top: 30px !important;
      padding-bottom: 0 !important;
      margin-bottom: 0;
    }

    //text-align: left;
    p {
      display: block;
      text-align: center;
      font-size: 16px;
      margin-bottom: 5px;

      span {
        font-size: 22px;
        font-weight: bold;
        display: inline-block;
        width: 100%;
        padding: 6px 0 10px;
        color: #ec0000;
      }
    }
  }

  .logowanie.broke {
    h1 {
      text-align: center;
      display: block;
      padding-top: 30px !important;
    }

    p {
      font-size: 16px;
      text-align: center;
      padding-bottom: 20px;
    }
  }

  &-loader {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    background: rgba(255, 255, 255, .5);

    .loader_box {
      width: 40px;
      height: 40px;

      .loader {
        width: 100%;
        height: 100%;
        border: .2em solid #ec0000;
        border-bottom-color: transparent;
        border-radius: 50%;
        animation: .6s loader-03 linear infinite;
      }
    }
  }

  @media screen {
    .help-line {
      width: 100%;
      max-width: 1055px;
      position: relative;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      align-content: stretch;
      margin: 10px auto;
      text-align: left;
    }
    .help-line .text {
      background-color: #e9f3f7;
      border-radius: 5px;
      position: relative;
      font-size: 13px;
      flex-grow: 1;
    }
    .help-line .text .primary {
      padding: 21px 17px 17px 45px;
      border-radius: 4px;
      min-height: 63px;
      box-sizing: border-box;
    }
    .help-line .text .information-panel-icon {
      background: url("https://www.centrum24.pl/centrum24-web/css/ico/137E84/black-info.svg") no-repeat 17px 17px;
      padding-left: 75px !important;
      background-size: 30px 30px;
    }
    .help-line.hmp-warning .text {
      background-color: #fff0c1;
    }
    .help-line.hmp-warning .text .information-panel-icon {
      background-image: url("https://www.centrum24.pl/centrum24-web/css/ico/946F00/dark-yellow-info.svg");
    }
    .help-line .text {
      color: #2e3035;
    }
  }

  .obsolete-browser-container {
    position: relative;
    max-width: 860px;
    margin: 0 auto;
  }


  /*! CSS Used from: Embedded */

  .lang_container {
    display: flex;
    align-items: center;
  }

  ul#lang {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    list-style: none;
    font-size: 16px;
    line-height: 20px;
  }

  ul#lang li {
    flex-grow: 1;
    flex-basis: auto;
    padding: 0 0.5em;
    text-align: center;
    border-left: 1px solid #137E84;
  }

  ul#lang li:first-of-type {
    border-left: none;
  }

  ul#lang li:last-of-type {
    padding-right: 1px;
  }

  ul#lang li a:disabled {
    font-weight: bold;
    cursor: pointer;
  }

  ul#lang li a,
  ul#lang li span {
    font-weight: normal;
    color: #137E84;
  }

  ul#lang li span {
    font-weight: bold;
    pointer-events: none;
  }

  #tipGainAccess p {
    font-size: 11px;
    margin-left: 40px;
    margin-right: 40px;
  }

  #tipGainAccessDiv {
    background: #F4F4F5;
    margin-bottom: 20px;
    margin-top: 20px;
    padding: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  #tipGainAccessDiv > div {
    width: 50%;
  }

  #tipGainAccessDiv p {
    margin: 0;
  }

  #tipGainAccessDiv > div:nth-child(2n - 1) {
    border-right: 1px solid #ECECED;
  }

  #tipGainAccessDiv > div:nth-child(2n - 1) p {
    padding-left: 40px;
    padding-right: 20px;
  }

  #tipGainAccessDiv > div:nth-child(2n) p {
    padding-left: 20px;
    padding-right: 40px;
  }

  #accessDiv div.modal-body {
    padding-bottom: 20px;
    padding-top: 0;
    padding-left: 15px;
    padding-right: 15px;
  }

  #accessDiv div.modal-header {
    padding-bottom: 0;
  }

  #tipGainAccess {
    width: 566px;
  }

  #exclamation-icon {
    float: left;
    margin: 0 5px -5px 0 !important;
  }

  .modal.fade {
    opacity: 0;
    -webkit-transition: opacity .15s linear;
    -o-transition: opacity .15s linear;
    transition: opacity .15s linear;
  }

  .modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999980;
    display: none;
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
    outline: 0;
  }

  .modal.fade .modal-dialog {
    -webkit-transition: -webkit-transform .3s ease-out;
    -o-transition: -o-transform .3s ease-out;
    transition: transform .3s ease-out;
    -webkit-transform: translate3d(0, -25%, 0);
    -o-transform: translate3d(0, -25%, 0);
    transform: translate3d(0, -25%, 0);
  }

  .modal-dialog {
    position: relative;
    width: 600px;
    margin: 30px auto;
  }

  .modal-content {
    position: relative;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #999;
    border: 1px solid rgba(0, 0, 0, .2);
    outline: 0;
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    padding: 0 20px 20px 20px;
    border-radius: 0 !important;
  }

  .modal-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    border-bottom: 1px solid #9bc3d3 !important;
    margin-bottom: 20px;
    padding: 0 !important;
  }

  .modal-header .close {
    align-self: flex-start;
    flex-shrink: 0;
    cursor: pointer;
    z-index: 1000;
    background: url(https://www.centrum24.pl/css/images/close-black.svg) no-repeat 0 0 !important;
    background-size: 15px 15px !important;
    width: 15px !important;
    height: 15px !important;
    margin-top: 20px !important;
    opacity: 1 !important;
    border: 0 !important;
  }

  .modal-header img {
    float: left;
    margin: 5px 10px 0 0 !important;
    padding: 0 !important;
    align-self: flex-start;
  }

  .modal-header-icon {
    padding: 15px 0 15px 15px;
  }

  .modal-title {
    margin: 0;
    border: 0;
    font-size: 18px;
    color: #444444;
    font-weight: bold;
    padding: 10px 0 !important;
    line-height: 38px;
  }

  .modal-title span {
    color: #444444;
    font-size: 18px;
    font-weight: bold;
    line-height: 38px;
    width: 485px;
  }

  .modal-body {
    position: relative;
    padding: 0 !important;
  }

  .modal-header:after,
  .modal-header:before {
    display: none !important;
  }

  @media screen {
    .button {
      display: inline-block;
      padding: 0 20px !important;
      margin: 5px 10px 5px 0;
      font-family: SantanderMicroTextW05, SantanderTextW05, 'Open Sans', Arial, sans-serif !important;
      font-size: 14px;
      text-align: center;
